import { binanceWithdraw } from "./markets/binance";
import { ExchangeMarket } from "./enums";
import { WithdrawManyData } from "./types";
import { okxWithdraw } from "./markets/okx";
import { bybitWithdraw } from "./markets/bybit";
import { gateWithdraw } from "./markets/gate";
import { kucoinWithdraw } from "./markets/kucoin";
import { mexcWithdraw } from "./markets/mexc";
import { huobiWithdraw } from "./markets/huobi";

const sleep = (ms: number): Promise<void> => {
  return new Promise((resolve) => setTimeout(resolve, ms));
};

const getRandomFloat = (min: number, max: number): number => {
  return Number(Math.random().toFixed(5)) * (max - min + 1) + min;
};

const exchangeMarketWithdrawMap = {
  [ExchangeMarket.BINANCE]: binanceWithdraw,
  [ExchangeMarket.OKX]: okxWithdraw,
  [ExchangeMarket.BYBIT]: bybitWithdraw,
  [ExchangeMarket.GATE]: gateWithdraw,
  [ExchangeMarket.HUOBI]: huobiWithdraw,
  [ExchangeMarket.KUCOIN]: kucoinWithdraw,
  [ExchangeMarket.MEXC]: mexcWithdraw,
};

export const exchangeMarketWithdrawToMany = async (data: WithdrawManyData) => {
  data.setStatus(
    `/// Started withdrawing from ${data.exchangeMarket} per ${data.amountFrom}-${data.amountTo} ${data.token}:\n`
  );

  for (const wallet of data.wallets) {
    await sleep(getRandomFloat(data.delayFrom, data.delayTo) * 1000);

    const amount = getRandomFloat(data.amountFrom, data.amountTo);

    await exchangeMarketWithdrawMap[data.exchangeMarket]({
      ...data,
      amount,
      wallet,
    });
  }

  data.setStatus((prev: string) => `${prev}/// Done!`);
};
