import { Button, Table } from "antd";
import type { ColumnsType } from "antd/es/table";
import { FC } from "react";

interface DataType {
  key: string;
  email: string;
}

const columns: ColumnsType<DataType> = [
  {
    title: "Email",
    dataIndex: "email",
    key: "email",
  },
  {
    title: "Action",
    key: "action",
    render: (_, record) => (
      <Button type="primary" danger>
        Delete
      </Button>
    ),
  },
];

const data: DataType[] = [
  {
    key: "1",
    email: "John Brown",
  },
  {
    key: "2",
    email: "Jim Green",
  },
  {
    key: "3",
    email: "Joe Black",
  },
];

const AllUsers: FC = () => <Table columns={columns} dataSource={data} />;

export default AllUsers;
